.app-wrapper {
    margin: 0 auto;
    display: grid;
    /*width: 1300px;*/

    grid-template-areas:
    "h h "
    "n c";

    grid-template-rows: 50px 1fr;
    grid-template-columns: 2fr 10fr;
    /*grid-gap: 10px;*/
}

.app-wrapper-content {
    grid-area: c;
    opacity: 90%;
    background: lavender;

    /*box-shadow: 0 0 10px;*/
    /*background-color: lavender;*/
}
